.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}


/* ======= CUSTOM STYLES ======== */

input:invalid {  background-color: #ffdddd; }

.navbar{
	width:100%;
	font-family:"Helvetica Neue",Helvetica,Arial,sans-serif;
	height:35px;
	background-color:#080808;
	color:#428bca;
	font-size:24px;
	line-height:35px;
	cursor:default;
	-webkit-box-shadow:0 0 5px rgba(0,0,0,.8);
	        box-shadow:0 0 5px rgba(0,0,0,.8);
	position:relative;
	z-index:1;
}

.referenceLink {
	padding:0px 5px;
	font-family:"Helvetica Neue",Helvetica,Arial,sans-serif;
	height:35px;
	background-color:#e02e0b;
	display:inline-block;
	color:white;
	font-size:24px;
	line-height:35px;
	cursor:pointer;
	-webkit-box-shadow:0 0 5px rgba(0,0,0,.8);
	        box-shadow:0 0 5px rgba(0,0,0,.8);
	float:right;
	margin-right: 30px;
}

.smallBreak { line-height:15px; }

#canvasWrapper {
	  overflow:hidden;
	  height:70vh;
	  position:relative;
}

.bottom{
	/*position: fixed;*/
	font-size:14px;
	width:100%;
	padding-top:5px;
}

#scaleunits { vertical-align:top; }


.bubble {     /*lies within the fixed ".bottom" */
    margin:0 auto;
	font-size:13px;
	background-color:white;
	border-radius:10px;
	padding:5px;	
	width:250px;
}

.InputStyle {
	font-size:17px;
	padding:1px 3px;
	width:80px;
}

#angle { width:49px; }

/*130 works in Chrome, but looks terrible in FireFox.*/
.slider{ width:124px; }

.UnitsStyle {
	display:inline-block;
	padding:2px 5px 4px 5px;
	background-color:#ffefdb;
	width:25px;
	font-size:17px;
	color:#000000;
	border-radius:4px;
}

.InputBox {
	margin-top:20px;
	text-align:left;
	width:281px;
}

.range {
	font-size:11px;
	color:white;
}

.randomFact{
	font-size:14px;
	width:125px;
	color:#adccff;
	padding: 0px 5px;
}

.RadioBox {
	font-size:14px;
	text-align:center;
}

.ResetButton {
	background-color: #4CAF50; /* Green */
	border: none;
	color: white;
	padding: 10px 32px;
	text-align: center;
	text-decoration: none;
	display: inline-block;
	font-size: 16px;
	border-radius: 5px;
	margin-top:20px;
}

/*Remove the useless {0} value bubble in Edge browser.*/
::-ms-tooltip { display: none; }

.hide { display:none; }

#outer-container {
	height:100vh;
	overflow:hidden;
}

.distortionwarning { margin: 10px;
					font-size:0.8em; 
					color:#f49842; }

body { background-color:#9e9e9e; }

#inputlabel { left:62px; }
#outputlabel { right:62px; }

#inputlabel, #outputlabel {
	position:fixed;
	top:60px;
	font-size:1.1em;
}

#page-wrap {
	border:2px solid black;
	height:99.5vh;
}

/*CSS for the output menu */
#outeroutputtable { font-size: 0.8em; color: black; border:1px solid #000; text-align:center; background-color:white; border-radius:5px; width:290px; padding:5px 0px; margin:0 auto;}
#outputbox h3 { -webkit-margin-before: 0.5em; -webkit-margin-after: 0.5em; }
#innertable { border-collapse:separate; border-spacing:10px 0px; }
.border_bottom td { border-bottom:1px solid #000; }
.redcolor { color:#CE0D45; }
.bluecolor { color:#2F329F; }
.borderright { text-align:right; border-right: 1px solid #000; vertical-align:middle; padding-top:7px; padding-bottom:7px; padding-right:10px; }
.output1 { /*text-align:right;*/ vertical-align:bottom; margin-bottom:0px; display:inline-block; }
#boldrow { font-weight:bold; }

#secondtable {border-collapse:separate; border-spacing:3px 13px; margin:0em 0em 0em 0.6em;}
#radiospeed { margin: 10px 0px 10px 8px; font-size: 1em; color:#adbdff;}
#innertable2 { border-collapse:separate; border-spacing:8px 0px; }
.leftoutput { width:180px; color:#adbdff;    }
.border_bottom2 td { border-bottom:1px solid #000; color:#2F329F; font-weight:bold;}
.conditionalBreak { line-height:15px; }
#conditionalBreak1 { line-height:15px; }


/*Mobile screens tend to have things like
title bars, etc that shrink the size "seen."
I do not want the titlebar to scroll, because it seems glitchy. 
Shorten the menus to fit on mobile screens.*/
@media screen and ( max-height: 690px ){
	#canvasWrapper {
		height:60vh;
	}
	
	#outer-container {
		height:88vh;
		overflow:hidden;
	}
	
	#page-wrap { border:none; }
	
	.bm-menu {
	  padding-top: 0em!important;
	  padding-bottom:0em!important;
	}
	
	.InputBox { margin-top:9px; 
				font-size:.85em; }
	
	h3 {
		-webkit-margin-before: 0.5em;
		-webkit-margin-after: 0.5em;
	}
	
	.ResetButton {
		background-color: #4CAF50; /* Green */
		border: none;
		color: white;
		padding: 7px 15px;
		text-align: center;
		text-decoration: none;
		display: inline-block;
		font-size: 14px;
		border-radius: 5px;
		margin-top:10px;
	}
	
	#radiospeed { margin: 5px 0px 5px 8px; }
	#secondtable { font-size:0.9em;  border-spacing: 5px 9px; }
	.conditionalBreak { line-height:0px; }
	#conditionalBreak1 { line-height:5px; }
	
	html.touch body { overflow:hidden; }
}

/*On landscape media screens, allow the titlebar to scroll away 
 (unfortunately only works in Chrome mobile) */
@media screen and ( max-height: 450px ){
	.bubble { display:none;}
	
	#canvasWrapper { height:78vh; }
	
	html.touch body { overflow:initial; }
	#outer-container { height:100vh; overflow:initial; }
	
	.left-button, .right-button {  top: 45px!important;	}
	#inputlabel, #outputlabel {  top: 50px!important;	}

	.navbar {
		height:25px;
		font-size:17px;
		line-height:25px;
	}
	
	#scaleunits { display:inline-block; }	
}




/*Fix the reference link on skinny screens*/
@media screen and (max-width:650px)
{
	#scaleunits { display:inline-block; }
	
	.referenceLink{
		height:25px;
		font-size:17px;
		line-height:25px;
		margin-right: 0px;
		margin-left: 20px;
		float:none;
	}
}

/*Fix the titlebar on even skinnier screens*/
@media (max-width: 500px)
{
	.left-button, .right-button {  top: 45px!important;	}
	#inputlabel, #outputlabel {  top: 50px!important;	}
	
	.navbar {
		height:25px;
		font-size:17px;
		line-height:25px;
	}
}

/*On landscape media screens made even shorter by titlebars,
eliminate this page's title to help a bit. 
This must come AFTER the width declaration,
so these important flags get higher precedence.*/
@media screen and ( max-height: 270px ){
	.navbar { display:none; }
	#canvasWrapper { height: 82vh;}
	
	.left-button, .right-button {  top: 15px!important;	}
	#inputlabel, #outputlabel {  top: 20px!important;	}

}



/* ======= SIDEBAR ====== */

/* Position and sizing of burger button */
/*.bm-burger-button {*/
.left-button {
  position: fixed;
  width: 36px;
  height: 30px;
  left: 20px;
  top: 55px;
}

.right-button {
  position: fixed;
  width: 36px;
  height: 30px;
  right: 20px;
  top: 55px;
}

/* Color/shape of burger icon bars */
.bm-burger-bars {
  /*background: #373a47;*/
  background: #505359;
}

/* Position and sizing of clickable cross button */
.bm-cross-button {
  height: 24px;
  width: 24px;
}

/* Color/shape of close button cross */
.bm-cross {
  background: #bdc3c7;
}

.bm-menu-wrap {
	/*overflow-y:hidden;*/
	width:315px!important;
}

/* General sidebar styles */
/*body.mozilla .bm-menu-left
{  padding-left:0px; }

@-moz-document url-prefix() {}
*/

#CenteredLeftMenu {
	width:271px; /*smaller than what it contains*/
	margin: 0 auto;
}

/*counteract the deleterious effect of other stylesheets I may attach later.*/
.bm-menu, .bm-menu *:before, .bm-menu *:after {
	box-sizing:initial!important;
	-webkit-box-sizing: initial!important;
	font-family:sans-serif;
	line-height:initial;
}

.bm-menu-left {
  background: #505359;
  padding: 0.5em 0em 1em 0em;
  font-size: 1.05em;
}

.bm-menu-right {
  background: #505359;
  padding: 0.5em 0em 0em 0em;
  font-size: 1.05em;
  overflow-x: hidden!important;
}


/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
  fill: #505359;
}

/* Wrapper for item list */
.bm-item-list {
  margin:0 auto;
  color: #b8b7ad;
  height:initial!important;
}

/* Styling of overlay */
.bm-overlay {
  background: rgba(0, 0, 0, 0.15)!important;  /*0.3);*/
}


