
#article-body {
	/*height: 99.5vh;*/
	/*width: 70vw;*/
	width:700px;
	/*margin: 0 auto;*/
	color:black;
	background-color: white;
	background-image: url(img/diamond_upholstery.png);
	/*opacity:0.9;*/
	border: 1px solid #000000;
	-webkit-box-shadow:0 0 10px rgba(0,0,0,.8);
	        box-shadow:0 0 10px rgba(0,0,0,.8);
	text-align:left;
	overflow:auto;
	padding:20px;
	font-family: "Open Sans", Arial, Helvetica, sans-serif;
	font-size:1.05em;
		
	z-index:2;
	position:fixed;
	top:-1px;
	bottom:-1px;
	left:50%;
	-webkit-transform: translate(-50%, 0);
	    -ms-transform: translate(-50%, 0);
	        transform: translate(-50%, 0);


}

#TOC {
	padding:15px 5px 10px 5px;
	border-radius:10px;
	opacity:0.85;
	text-align:left;
	background-color:white;
	z-index:2;
	width:220px;
	position:fixed;
	left:50%;
	-webkit-transform: translate(-605px, 0);
	    -ms-transform: translate(-605px, 0);
	        transform: translate(-605px, 0);
	top:30px;
	font-size:.95em;
}

.TOCLink { cursor:pointer; color:#0f54c4; font-weight:bold; padding:7px;}
.TOCLink:hover { background-color: #cddaf4; border-radius:5px;}

.subtopic { color:#6065a0; font-weight:initial; list-style-type:square; padding:0px; margin:5px 0px 5px 20px; }

#centeredArticle {}

#article-body h1, #article-body h2, #article-body h3, #article-body h4, #article-body h5, #article-body h6 {
	font-family: "Lato";
	font-weight: bold;
	color: #0f54c4
}

/*list elements*/
#article-body ul {
	list-style-type: none;
	padding: 0;
	margin: 0;  }
#article-body ul li {
	/*list-style-image: url(img/bullet.png);*/
    /*line-height: 1.125rem;*/  
	background-image: url(img/star.png);
    background-repeat: no-repeat;
    background-position: 0 0.1rem;
    padding-left: 1.75rem;
}

@media screen and (max-width:1220px) {
	#article-body { -webkit-transform:translate(-35%,0); -ms-transform:translate(-35%,0); transform:translate(-35%,0); }
	#TOC { -webkit-transform: translate(-490px,0); -ms-transform: translate(-490px,0); transform: translate(-490px,0); }
}

@media screen and (max-width:980px) {
	#article-body { width:500px; }
	#TOC { -webkit-transform: translate(-422px,0); -ms-transform: translate(-422px,0); transform: translate(-422px,0); }	
}

@media screen and (max-width:860px) {
	#article-body { -webkit-transform:translate(-28.5%,0); -ms-transform:translate(-28.5%,0); transform:translate(-28.5%,0) }
	#TOC { -webkit-transform: translate(-385px,0); -ms-transform: translate(-385px,0); transform: translate(-385px,0); }	
}

@media screen and (max-width:780px) {
	#article-body { -webkit-transform:translate(-50%,0); -ms-transform:translate(-50%,0); transform:translate(-50%,0) }
	#TOC { display:none; }	
}


@media screen and (max-width:650px) {
	#article-body { left:initial; width:initial; -webkit-transform:initial; -ms-transform:initial; transform:initial; }
	img {max-width:100%;}
	
	#TOC {
		display:none;
	}
}

@media screen and (max-height:720px) {
	#TOC { top:0px	}
}


.hide {display:none;}

.center { text-align:center; }

#smallMargin { margin: 5px 0px 5px 0px; }
.smallText { font-size: .75em; line-height:1em; margin-top:-10px;}
.center { text-align:center; }

.floatleft, .floatright, .center img, .floatleft img, .floatright img {
	border-radius:7px; 
}

.floatleft {  float: left;  margin-right:10px; }
.floatright {  float: right;  margin-left:10px; }


@media (max-width: 450px)
{	img, .image, .floatright, .floatleft {
		float:none;
	}
}

.overlay {
	position: fixed;
	z-index: 1;
	width: 100%;
	height: 100%;
	top:0px;
	left:0px;
	background: rgba(0, 0, 0, 0.3);
	opacity: 1;
	cursor:pointer;
}


/* Position and sizing of clickable cross button */
.articleX {  /*.bm-cross-button*/
  height: 25px!important;
  width: 25px!important;
  /*background-color:red;*/
  background-color:#f44242;
  border-radius:3px;
  z-index:2;
  -webkit-box-shadow:4px 4px 8px rgba(200,0,0,3);
          box-shadow:4px 4px 8px rgba(200,0,0,3);
  right:20px!important;
}

/* Color/shape of close button cross */
.bm-cross {
  background: #bdc3c7;
}


